import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import MyContext from './MyContext';
import portfolio from '../services/portfolio';
import headerLinks from '../utils/headerLinks';

function Provider({ children }) {

  const history = useHistory();

  const [OS, setOS] = useState('Linux');

  useEffect(() => {
    let name = 'Linux';
    if (navigator.userAgent.indexOf('Win') !== -1) { name = 'Windows'; }
    setOS(name);
  }, []);
  
  const [width, setWidth] = useState(window.innerWidth);

  function handleWindowResize() {
    setWidth(window.innerWidth);
  }

  useEffect(() => {
    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  const [links, setLinks] = useState({
    currentLink: {},
    otherLinks: [],
  });

  useEffect(() => {
    const { pathname } = history.location;
    const currentLink = headerLinks.find((el) => el.path === pathname);
    const otherLinks = headerLinks.filter((el) => el.path !== pathname);
    setLinks({ currentLink, otherLinks });
  }, [history.location]);

  function handleLinks(path) {
    const currentLink = headerLinks.find((el) => el.path === path);
    const otherLinks = headerLinks.filter((el) => el.path !== path);
    setLinks({ currentLink, otherLinks });
    history.push(path);
  }

  const [projects, setProjects] = useState({
    projectsList: [],
    renderedProjects: [],
    projectId: 0,
  });

  const [search, setSearch] = useState({
    projectCategory: '',
    projectTech: '',
  });

  useEffect(() => {
    setProjects((prevState) => ({
      ...prevState, projectsList: [...portfolio], renderedProjects: [...portfolio],
    }));
  }, []);

  useEffect(() => {
    let filteredPortfolioByCategory = [...projects.projectsList];
    if (search.projectCategory === 'all') { filteredPortfolioByCategory = [...projects.projectsList]; }
    if (search.projectCategory === 'frontend') { filteredPortfolioByCategory = filteredPortfolioByCategory.filter((el) => el.category.includes('Front-End')); }
    if (search.projectCategory === 'backend') { filteredPortfolioByCategory = filteredPortfolioByCategory.filter((el) => el.category.includes('Back-End')); }    
    const filteredPortfolioByName = filteredPortfolioByCategory.filter((el) => el.techs.toLowerCase().includes(search.projectTech.toLowerCase()));
    const newRenderedProjects = (filteredPortfolioByName.length > 0) ? filteredPortfolioByName : filteredPortfolioByCategory;
    setProjects((prevState) => ({
      ...prevState, renderedProjects: [...newRenderedProjects], projectId: 0,
    }));
  }, [projects.projectsList, search.projectCategory, search.projectTech]);

  function handleSearch({ target }) {
    const { name, type, value } = target;
    setSearch((prevState) => {
      return {
        ...prevState,
        [name]: (type === 'checkbox') ? !prevState[name] : value,
      }      
    });  
  }

  const contextValue = {
    OS,
    width,
    links,
    handleLinks,
    projects,
    setProjects,
    search,
    handleSearch,
  }

  return (
    <MyContext.Provider value={ contextValue }>
      { children }
    </MyContext.Provider>
  );
}

export default Provider;
