import React, { useContext, useState } from 'react';
import MyContext from '../../context/MyContext';
import { handleTouchStart, handleTouchEnd } from '../../helpers/onTouchFunctions';
import { GoChevronLeft, GoChevronRight } from "react-icons/go";
import workInProgress from '../../images/workInProgress.png';
import './style.css';

function Portfolio() {

  const isFinished = false;

  const { renderedProjects, projectId } = useContext(MyContext).projects;
  const { projectTech } = useContext(MyContext).search;
  const { setProjects, handleSearch } = useContext(MyContext);

  function goToPrevious() {
    setProjects((prevState) => {
      if (prevState.projectId > 0) {
        return {
          ...prevState,
          projectId: prevState.projectId - 1,
        }
      } else {
        return {
          ...prevState,
          projectId: renderedProjects.length - 1,
        }
      }
    });
  }

  function goToNext() {  
    setProjects((prevState) => {
      if (prevState.projectId < renderedProjects.length - 1) {
        return {
          ...prevState,
          projectId: prevState.projectId + 1,
        }
      } else {
        return {
          ...prevState,
          projectId: 0,
        }
      }
    });
  }

  const [touchstartX, setTouchstartX] = useState(0);
  const [touchendX, setTouchendX] = useState(0);

  function handleRenderedProjects() {
    if (touchendX < touchstartX) {
      goToNext();
    }
    if (touchendX > touchstartX) {
      goToPrevious();
    }
  }

  function handleSwipeStart(event) {
    const touchstart = event.changedTouches[0].screenX;
    setTouchstartX(touchstart);
  }

  function handleSwipeEnd(event) {
    const touchend = event.changedTouches[0].screenX;
    setTouchendX(touchend);
    handleRenderedProjects();
  }

  return (
    <div className="portfolio-container">
      {
        isFinished
        ? (
          <>
            <div className="portfolio-sub-container">  
              <h1 className="portfolio-heading">Meus Projetos</h1> 
              <div className="portfolio-text">
                <p>Esta aplicação usa React com componentes funcionais, Hooks e Context API e CSS.</p>
                <p>No entanto, os projetos abaixo podem utilizar alternativamente JavaScript puro com HTML e CSS, React com componentes de classe e State Lifting ou Redux.</p>
                <p>É possível escolher os projetos por tecnologias utilizadas abaixo:</p>
              </div>
              <div className="portfolio-radio">
                <fieldset>
                  <legend>Categoria</legend>
                  <label>
                    <input
                      name="projectCategory"
                      type="radio"
                      value="all"
                      onChange={ handleSearch }
                    />
                    Todas
                  </label>
                  <br />
                  <label>
                    <input
                      name="projectCategory"
                      type="radio"
                      value="frontend"
                      onChange={ handleSearch } 
                    />
                    Front-End
                  </label>
                  <br />
                  <label>
                    <input
                      name="projectCategory"
                      type="radio"
                      value="backend"
                      onChange={ handleSearch }
                    />
                    Back-End
                  </label>
                </fieldset>
              </div>
              <input
                name="projectTech"
                type="text"
                placeholder="Busque uma tecnologia"
                value={ projectTech }
                onChange={ handleSearch }
                className="portfolio-search-input"
              />
            </div>
            {
              (renderedProjects.length > 0)
              ? (
                  <div className="portfolio-content-list">
                    <button
                      type="button"
                      onClick={ goToPrevious }
                      className="portfolio-content-btn"   
                    >
                      <GoChevronLeft />
                    </button>
                    <div className="portfolio-content-outer-box">
                      <video src={ renderedProjects[projectId].video } controls muted className="portfolio-content-video" />
                      <div
                        onTouchStart={ (event) => handleSwipeStart(event) }
                        onTouchEnd={ (event) => handleSwipeEnd(event) }
                        className="portfolio-content-inner-box"
                      >
                        <p className="portfolio-content-info-one">{ renderedProjects[projectId].name }</p>
                        <p className="portfolio-content-info-two">Categoria: { renderedProjects[projectId].category }</p>
                        <p className="portfolio-content-info-two">Tecnologias: { renderedProjects[projectId].techs }</p>
                      </div>
                      <a
                        href={ renderedProjects[projectId].repository }
                        target="_blank"
                        rel="noreferrer"
                        onTouchStart={ (event) => handleTouchStart(event) }
                        onTouchEnd={ (event) => handleTouchEnd(event) }
                        className="portfolio-content-link">
                          Veja o repositório
                        </a>
                      <a
                        href={ renderedProjects[projectId].page }
                        target="_blank"
                        rel="noreferrer"
                        onTouchStart={ (event) => handleTouchStart(event) }
                        onTouchEnd={ (event) => handleTouchEnd(event) }
                        className="portfolio-content-link">
                          Veja a aplicação
                        </a>
                    </div> 
                    <button
                      type="button"
                      onClick={ goToNext }
                      className="portfolio-content-btn"    
                    >
                      <GoChevronRight />
                    </button>     
                  </div>
              )
              : (
                <div>
                  <img src={ workInProgress } alt="Projetos em Desenvolvimento" className="portfolio-in-progress" />
                </div>
              )
            }
          </>
        ) : (
          <img src={ workInProgress } alt="Projetos em Desenvolvimento" className="portfolio-in-progress" />
        )
      }
    </div>
  );
}

export default Portfolio;
