import React, { useContext, useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
import MyContext from './context/MyContext';
import Home from './pages/Home';
import About from './pages/About';
import Portfolio from './pages/Portfolio';
import { handleTouchStart, handleTouchEnd } from './helpers/onTouchFunctions';
import { AiFillGithub, AiFillLinkedin } from 'react-icons/ai';
import './App.css';

function App() { 

  const { currentLink, otherLinks } = useContext(MyContext).links;
  const { OS, handleLinks } = useContext(MyContext);  

  useEffect(() => {
    if (OS === 'Windows') { document.getElementsByTagName('html')[0].style.fontSize = '13px'; }
  }, [OS]);

  function renderLinks() {
    return (
      <nav className="header-nav">
        {
          (currentLink && otherLinks)
          ? (
              <>
                <button
                  type="button"
                  onClick={ () => handleLinks(currentLink.path) }
                  className="header-current-link"
                >
                  { currentLink.name }
                </button>
                <nav className="header-sub-nav">
                  { 
                    otherLinks.map((el, i) => (
                      <button
                        key={ i }
                        type="button"
                        onClick={ () => handleLinks(el.path) }
                        onTouchStart={ (event) => handleTouchStart(event) }
                        onTouchEnd={ (event) => handleTouchEnd(event) }
                        className="header-link"
                      >
                        { el.name }
                      </button>
                    ))
                  }
                </nav>
              </>
          )
          : (
              <>
                <button
                  type="button"
                  onClick={ () => handleLinks("/my-page") }
                  className="header-current-link"
                >
                  Home
                </button>
                <nav className="header-sub-nav">
                  <button
                    type="button"
                    onClick={ () => handleLinks("/about") }
                    onTouchStart={ (event) => handleTouchStart(event) }
                    onTouchEnd={ (event) => handleTouchEnd(event) }
                    className="header-link"
                  >
                    Sobre
                  </button>
                  <button
                    type="button"
                    onClick={ () => handleLinks("/portfolio") }
                    onTouchStart={ (event) => handleTouchStart(event) }
                    onTouchEnd={ (event) => handleTouchEnd(event) }
                    className="header-link"
                  >
                    Portifólio
                  </button>
                </nav>
              </>
          )
        }
      </nav>
    );
  }

  function renderRoutes() {
    return (
      <Switch>
        <Route path="/portfolio" component={ Portfolio } />
        <Route path="/about" component={ About } />
        <Route path="/" component={ Home } />
      </Switch>
    );
  }

  function setYear() {
    const today = new Date();
    const currentYear = today.getFullYear();
    return currentYear;
  };
  
  return (
    <div id="parent-div">
      <header>
        { renderLinks() }
      </header>
      <main>
      { renderRoutes() }
      </main>
      <footer>        
        <p className="footer-text">&copy; Copyright Rhenan Stocco Zimmermann - { setYear() }</p>
        <nav className="footer-nav">
          <a
            href="https://github.com/rhenanstoccozimmermann"
            target="_blank"
            rel="noreferrer"
            onTouchStart={ (event) => handleTouchStart(event) }
            onTouchEnd={ (event) => handleTouchEnd(event) }
            className="footer-link"
          >
            GitHub <AiFillGithub />
          </a>
          <a
            href="https://www.linkedin.com/in/rhenanstoccozimmermann/"
            target="_blank"
            rel="noreferrer"
            onTouchStart={ (event) => handleTouchStart(event) }
            onTouchEnd={ (event) => handleTouchEnd(event) }
            className="footer-link"
          >
            LinkedIn <AiFillLinkedin />
          </a>   
        </nav>
      </footer>
    </div>
  );
}

export default App;
